
.tx {
	position: absolute;
	right: 90px;
	height: 50px;
	width: 50px;
	border-radius: 50px;
	border: 1px solid silver;
	overflow: hidden;
	cursor: pointer;
}
.tx img {
	width: 50px;
	height: 50px;
}
.xx{
	margin-top: 3px;
	font-size: smaller;
	position: absolute;
	width: 10em;
	right: 50px;
	top: 50px;
}
#login{
	border: 1px solid silver;
	z-index: 3;
	background-color:#F0F0F0 ;
	height: 86px;
	padding: 4px;
	position: absolute;
	top: 96px;
	right: 8px;
}



